.container {
  scroll-snap-type: y mandatory;
  overflow-y: auto;
  overflow-x: hidden;

  border: 2px solid var(--gs0);
  border-radius: 8px;
  height: 100vh;
}

.container div {
  scroll-snap-align: start;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 4rem;

  text-align: center;
}
.first {
  border: 3px solid green;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.first h1,
.first h3 {
  margin: 0;
  font-size: 5vh;
}

.first h3 {
  font-size: 3vh;
}

.container div:nth-child(1) {
  background: hotpink;
  color: white;
}
.container div:nth-child(2) {
  background: aqua;
}
.container div:nth-child(3) {
  background: blanchedalmond;
}
.container div:nth-child(4) {
  background: lightcoral;
  color: white;
}
/*
* NAVIGATION CSS
*/
.nav_container {
  width: 100%;
  margin: 20px auto;
}

.center {
  text-align: center;
}

.nav h1 {
  margin-left: 20px;
  font-family: "Ink Free", cursive;
  color: black;
}
.nav {
  box-shadow: 0px 2px 24px 0px rgba(0, 0, 0, 0.15);
  display: flex;
  justify-content: center;
  width: 100%;
}

.div_links {
  display: flex;
}

.nav_links {
  margin: auto;
}

.nav_links li {
  display: flex;
  border-bottom: 3px solid black;
  font-family: "Ink Free", fantasy, cursive;
  font-size: 14px;
}

.nav_links a,
.nav a {
  text-decoration: none;
  color: white;
}

.nav li:hover {
  background-color: rgba(200, 99, 71, 0.5);
}

/*
* ABOUT ME CSS
*/
.about-me-grid {
  background: #06beb6; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #48b1bf,
    #06beb6
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #48b1bf,
    #06beb6
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  text-align: center;
  font-family: "Ink Free";
}
.about-me-cell {
  background-color: rgba(0, 0, 0, 0.8);
  color: white;
  padding: 50px 0 50px 0;
  border: 1px solid red;
}
.about-me-cell h2 {
  font-size: 45px;
  font-weight: bold;
  color: white;
  letter-spacing: 10px;
  font-family: "Forte";
  width: 60%;
  margin: auto;
}
.about-me-cell hr {
  width: 60%;
}
.paragraph {
  padding: 100px 0 100px 0;
  font-size: 18px;
  text-align: left;
  letter-spacing: 1.5px;
}
.paragraph p {
  color: white;
  width: 60%;
  margin: auto;
  border-radius: 10px;
  border-top: 2px double rgba(255, 255, 255, 0.6);
  border-bottom: 2px double rgba(255, 255, 255, 0.6);
  border-width: thick;
  background-color: rgba(0, 0, 0, 0.5);
  text-align: center;
}

/* 
* For Average screens at least 720px 
*/
@media screen and (min-width: 720px) {
  .nav_links {
    display: flex;
    justify-content: center;
  }

  .nav_links li {
    border-bottom: none;
    padding: 28px 30px 22px 30px; /* top right bottom left */
    display: inline;
    font-size: 20px;
  }

  .about-me-cell hr {
    width: 30%;
  }
  .paragraph p {
    width: 30%;
  }
}

